import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CBadge,
  CDataTable,
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CSelect,
  CInputFile,
  CAlert
} from '@coreui/react'
import { rgbToHex } from '@coreui/utils'
import { DocsLink } from 'src/reusable';
import usersData from '../../users/UsersData'
import ModalForm from '../../../components/ModalForm/ModalForm';
import {getCourses, createCourse, deleteCourse, updateCourse} from '../../../services/coursesService';
import {getCategories} from '../../../services/categoriesService';
import ModalWarning from '../../../components/ModalWarning/ModalWarning';



const getBadge = highlight => {
  switch (highlight) {
    case true: return 'success'
    case false: return 'danger'
    default: return 'primary'
  }
}
const fields = [{key: 'name', value: 'nombre', sorter: true},'description', 'financial_knowledge', 'highlight', 'value', 'alumnos', 'tags', 'modules', 'actions']
const fields1 = { name:'Nombre', description: 'Descripción', financial_knowledge: 'Conocimientos Financieros', alumnos: 'Alumnos' ,value: 'Valor', highlight: 'Destacado', tags: 'Etiquetas' , actions:'Acciones', modules: 'Módulos' ,createdAt: 'Fecha'}
// const columnsFilter = { name:'Nombre', description: 'Descripcion', financial_knowledge: 'Conocimiento Financiero', value: 'valor', highlight: 'Destacado' }
// const fields2 = ['name']

const Courses = ({history}) => {
  const [visibleForm, setVisibleForm] = useState(false);
  const [deleteRow, setDeleteRow] = useState(false);
  const [editing, setEditing] = useState(false)
  const [error, setError] = useState(false)
  const [errorFile, setErrorFile] = useState(false)
  const [idForDelete, setIdForDelete] = useState(null);
  const [data, setData] = useState([]);
  const [categories, setCategories] = useState([]);
  const [isHighlight,setIsHighlight] = useState(false);
  const [loading, setLoading] = useState(null);
  const [values, setValues] = useState({
    name: '',
    description: '',
    test_url: '',
    highlight: isHighlight,
    financial_knowledge: '',
    value: '',
    category: '',
    image: '',
    files: '',
  });
  const toggle = ()=>{
    setEditing(false);
    setVisibleForm(!visibleForm);
    setErrorFile(false);
    setValues({
      name: '',
      description: '',
      test_url: '',
      highlight: '',
      financial_knowledge: '',
      value: '',
      category: '',
      clean: visibleForm ? true : false,
    })
}
 useEffect (() => {
  handleLogout()
  getCategoriesData();
},[])

const handleLogout = async () => {
 const token =  await localStorage.getItem('apitoken');
 if(token===null || token === undefined){
   history.push('/login');
 } else{
  getData();

 }
}

 const getData = async () => {
   try {
    setLoading(true);
    const response = await getCourses();
    await setData(response.data.reverse());
    if(response.data.length > 0) await setLoading(false);
   } catch(err){
     console.log(err)
   }
 }
 const getCategoriesData = async () => {
  try {
   setLoading(true);
   const response = await getCategories();
   await setCategories(response.data);
   if(response.data.length > 0) await setLoading(false);
  } catch(err){
    console.log(err)
  }
}


const storeCourse = async () => {
  let course = new FormData();
  if(values.name==='' ||
    values.description ==='' ||
    values.test_url ==='' ||
    values.financial_knowledge==='' ||
    values.highlight==='' ||
    values.value ==='' ||
    values.category === '' ||
    values.image === ''
    ){
      setError(true)
      setTimeout(()=> {
        setError(false);
      },6000)
  }else{
    course.append('name', values.name);
    course.append('description', values.description);
    course.append('test_url', values.test_url);
    course.append('financial_knowledge', values.financial_knowledge);
    course.append('category', values.category);
    course.append('highlight', values.highlight);
    course.append('value', values.value);
    course.append('image', values.image);
  try {
      const response = await createCourse(course)
      if(response) {
          console.log(response.data, 'curso creado');
          setVisibleForm(!visibleForm);
          await getData();
          setError(false)
      }
  } catch(err){
      console.log(err)
  }
}
}

const deleteItem = (id) => {
  setDeleteRow(!deleteRow);
  setIdForDelete(id)
}

const handleDelete = async (id) => {
   console.log(id,'si entra al delete')
   try {
  if(id!==null){
  await deleteCourse(id)
}
  } catch (err) {
      console.log(err);
  }
  setDeleteRow(!deleteRow);
  await getData();
}

const handleEdit = (item) => {

  setVisibleForm(!visibleForm);
  setEditing(true);
  setError(false);
  setValues({...values,
      id: item.id,
      name: item.name,
      description: item.description,
      test_url: item.test_url,
      category: item.category,
      highlight: item.highlight,
      financial_knowledge: item.financial_knowledge,
      value: item.value,
      clean: visibleForm ? true : false,
      // image: '',
    })
}

const update = async () => {
  let course = new FormData();
  course.append('name', values.name);
  course.append('description', values.description);
  course.append('test_url', values.test_url);
  course.append('financial_knowledge', values.financial_knowledge);
  course.append('category', values.category);
  course.append('highlight', values.highlight);
  course.append('value', values.value);
  // course.append('image', values.image);
    if(values.id !== '' && values.name!=='' && values.description !=='' && values.test_url !==''  &&
    values.financial_knowledge!==''  &&
    values.highlight!==''  &&
    values.value !==''){
        try {

          const response = await updateCourse(course, values.id)
          if(response) {
              console.log(response.data, 'curso creado');
              setVisibleForm(!visibleForm);
              await getData();
              setEditing(false);
          }
        } catch(err) {
            console.log(err);
        }
    } else {
        setError(true);
        setTimeout(()=> {
          setError(false);
        },6000)
    }
}

const handleChange = (e) => {
  e.preventDefault();
  setValues({...values,
    [e.target.name] : e.target.value
  })
  console.log(values);

}

const filterCombo = () => {
  let filtered = categories.filter(item => item.id === values.category);
  return filtered.name;
}

const changeFile = (e) => {
  var file = e.target.files[0],
      pattern = /^image/,
      reader = new FileReader();

      if(!pattern.test(file.type)) {
          setErrorFile(true);
          console.log(e.target.files[0].name)
          // eslint-disable-next-line no-unreachable
          setValues({...values,
            image: '',
            clean: visibleForm ? true : false,
          })
          return;
      } else{
        setErrorFile(false);

  reader.onload = upload => {
  setValues({...values,
  image: upload.target.result,
  selectedFile: e.target.files[0].name,

  });
  console.log(e.target.files[0].name)


  console.log(values);
}
reader.readAsDataURL(file);
    console.log(values.image);
    console.log("Uploaded");

console.log(values.image);
}
}

  return (
    <>
      <CCard>
        <CCardHeader>
          {/* <DocsLink href="https://coreui.io/docs/utilities/colors/"/> */}
          <CButton
          color="primary"

      onClick={() => toggle()}
      className="mr-1"
    >Agregar Curso</CButton>
        </CCardHeader>
        <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Cursos
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={data}
              fields={fields}
              columnHeaderSlot={fields1}
              itemsPerPage={10}
              tableFilter={{label: 'Buscar', placeholder:'Ingresa tu Busqueda'}}
              loading={loading}
              pagination
              striped
              responsive
              scopedSlots = {{
                'highlight':
                  (item)=>(
                    <td>
                      <CBadge color={getBadge(item.highlight)}>
                        {item.highlight == true ? 'Si' : 'No'}
                      </CBadge>
                    </td>
                  ),
                  'actions':
              (item) => (
                <td style={{maxWidth: 200, minWidth: 200}}>
                  <CButton style={{marginRight: 10}} onClick={() => handleEdit(item)} color="warning">Editar</CButton>
                  <CButton onClick={() => deleteItem(item.id)} color="danger">Eliminar</CButton>
                </td>
              ),
              'modules':
              (item) => (
                <td style={{maxWidth: 100}}>
                  <CButton style={{maxWidth: 110}} onClick={() => history.push({pathname: '/cursos/modulos', id: item.id, course: item.name})} color="primary">Módulos</CButton>
                </td>
              ),
              'tags':
              (item) => (
                <td style={{maxWidth: 100}}>
                  <CButton style={{maxWidth: 110}} onClick={() => history.push({pathname: '/cursos/etiquetas', id: item.id, course: item.name})} color="primary">Etiquetas</CButton>
                </td>
              ),
              'alumnos':
              (item) => (
                <td style={{maxWidth: 100}}>
                  <CButton style={{maxWidth: 100}} onClick={() => history.push({pathname: '/cursos/alumnos', id: item.id, course: item.name})} color="primary">Alumnos</CButton>
                </td>
              ),
              'value':
              (item)=>(
                <td style={{maxWidth: 90, minWidth: 80}}>

                    $ {item.value}

                </td>
              ),
              'name':
              (item)=>(
                <td style={{maxWidth: 120, maxHeight:20, height: 20}}>

                    {item.name}

                </td>
              ),
              'description':
              (item)=>(
                <td style={{maxWidth: 120, maxHeight:20, height: 20}}>

                    {item.description}

                </td>
              ),
              'financial_knowledge':
              (item)=>(
                <td style={{maxWidth: 120, maxHeight:20, height: 20}}>

                    {item.financial_knowledge}

                </td>
              ),
              }
            }
            />
            </CCardBody>
          </CCard>
        </CCol>
      </CCard>
      <ModalForm visible={visibleForm} modalToggle={toggle} onSubmit={!editing ? () => storeCourse() : () => update()} title="Curso" >
      <CCol xs="12" sm="12">
          <CCard>
            <CCardBody>
              <CFormGroup>
                <CLabel htmlFor="name">Nombre</CLabel>
                <CInput id="name" name="name" value={values.name} onChange={(e) => handleChange(e)} placeholder="Nombre del curso" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="description">Descripción</CLabel>
                <CInput id="description" name="description" value={values.description} onChange={(e) => handleChange(e)} placeholder="Descripción" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="description">URL Evaluación</CLabel>
                <CInput id="test_url" name="test_url" value={values.test_url} onChange={(e) => handleChange(e)} placeholder="URL Evaluación" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="category">Categoría</CLabel>
                <CSelect onChange={(e) => handleChange(e)} value={values.category} custom name="category" id="category">
                        <option value="">Selecciona</option>
                      {categories.map((item, idx) => (
                        <option key={idx} value={item.id}>{item.name}</option>
                      ))}
                    </CSelect>
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="financial_knowledge">Conocimiento Financiero</CLabel>
                <CSelect onChange={(e) => handleChange(e)} value={values.financial_knowledge} custom name="financial_knowledge" id="financial_knowledge">
                  {editing ? <>
                    <option value="">Selecciona</option>
                     <option value="Básico">Básico</option>
                      <option value="Medio">Medio</option>
                      <option value="Avanzado">Avanzado</option> </> :
                       <>
                      <option value="">Seleccionar</option>
                      <option value="Básico">Básico</option>
                      <option value="Medio">Medio</option>
                      <option value="Avanzado">Avanzado</option> </>}

                    </CSelect>
              </CFormGroup>
              <CFormGroup row>
                  <CCol md="3">
                    <CLabel> ¿Destacado?</CLabel>
                  </CCol>
                  <CCol md="9">
                    <CFormGroup variant="custom-checkbox" inline>
                    <CSelect onChange={(e) => handleChange(e)} value={values.highlight} custom name="highlight" id="highlight">
                  <>
                      <option value="">Selecciona</option>
                      <option value="true">Si</option>
                      <option value="false">No</option> </>
                    </CSelect>
                    </CFormGroup>
                  </CCol>
                </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="country">Valor</CLabel>
                <CInput type="number" name="value" onChange={(e) => handleChange(e)} value={values.value} id="value" placeholder="Valor" />
              </CFormGroup>
              <CFormGroup key={values.clean} row>
                  <CLabel col htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="12">
                    <CInputFile encType="multipart/form-data" onChange={(e) => changeFile(e)} id="image" name="image"/>
                  </CCol>
                </CFormGroup>
                { error ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Debes ingresar todos los campos!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                { errorFile ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Formato de archivo inválido, solo imágenes!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
            </CCardBody>
          </CCard>
        </CCol>
      </ModalForm>
      <ModalWarning visible={deleteRow} onSubmit={() => handleDelete(idForDelete)} modalToggle={() => deleteItem(null)} title="Eliminar Usuario">Estas seguro de eliminar este Usuario?</ModalWarning>
    </>
  )
}

export default Courses
